import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import logo from '../assets/imgs/nav/logo.png';
import laguageImg from '../assets/imgs/nav/language.png';
import navDB from '../json/Nav.json';
import moMenuImg from '../assets/imgs/nav/moMenu.png'
import mobileAsideImg01 from '../assets/imgs/aside/mobile/moLgCnsCloud.png';
import mobileAsideImg03 from '../assets/imgs/aside/mobile/moAws.png';
import mobileAsideImg04 from '../assets/imgs/aside/mobile/moGoogleCloud.png';
import mobileAsideImg05 from '../assets/imgs/aside/mobile/moMsAzure.png';
// import mobileAsideImg06 from '../assets/imgs/aside/mobile/moCloudXper.png';
import mobileAsideImg02 from '../assets/imgs/aside/mobile/moLgCnsCloud02.png';
import moArrowDownImg from '../assets/imgs/aside/mobile/moArrowDown.png';
import moArrowUpImg from '../assets/imgs/aside/mobile/moArrowUp.png';
import moArrowNextImg from '../assets/imgs/aside/mobile/moArrowNext.png';
import microArrowUp from '../assets/imgs/nav/microArrowUp.png';
import microArrowDown from '../assets/imgs/nav/microArrowDown.png';
// import asideImg05 from '../assets/imgs/aside/cloudXper.png';
// import asideDB from '../json/Aside.json';

const NavBar = ({ paths }) => {

   const [hrefPaths, setHrefPaths] = useState();
   const [selectedLanguage, setSelectedLanguage] = useState("KR");
   const [position, setPosition] = useState(window.scrollY + 10);
   const [visible, setVisible] = useState(true);
   const [openSelect , setOpenSelect] = useState(false);
   const [moMenuOpen , setMomenuOpen] = useState(false);
   const [moAsideList , setMoAsideList] = useState(false);
   const [microSiteBtn, setMicroSiteBtn] = useState(false);
   const [onMouseEvet , setonMouseEvet] = useState(true);

   const selectValues = [ "KR" , "EN" ]; // jp
   const microPaths = ["/aws", "/googleCloud" , "/msAzure", "/cloudXper"];

   const handleSelectClick = (e) => {
       const thisTarget = e.target.innerText;      
       if(thisTarget){ 
         setSelectedLanguage(thisTarget);
         setOpenSelect(false);
      }
}

  const openSelectBoxs = () => {
     
   if(openSelect === true){
      setOpenSelect(false);
      } else{
     setOpenSelect(true);
    }
  }

   const mobileMenuOpenEvet = () => {
            
         if(window.innerWidth <= 1024){
            if(moMenuOpen === true){
               setMomenuOpen(false);
            }else{
               setMomenuOpen(true);
               setMoAsideList(false);
            }
         } 
         
   }

   const mobileAsieOpenEvet = () => {
            
      if(window.innerWidth <= 1024){
         if(moAsideList === true){
            setMoAsideList(false);
         }else{
            setMoAsideList(true);
         }
      } 
      
}


const microSiteMouseOverEvet = () => {
     
   if(onMouseEvet === true){
      if(visible === true){
         return setVisible(false)
      }else{
         return setVisible(true)
      }
   }

}

const microSiteBtnClickEvet = () => {
     
   if(onMouseEvet === false){
      if(visible === true){
         return setVisible(false)
      }else{
         return setVisible(true)
      }
   }

}


   useEffect(() => {

      const valuesEvet = () => {
       
         const selectBoxs = document.getElementById("languageSelectBox"); //
         selectBoxs.value = selectedLanguage;
         console.log('selectBoxValue' , selectBoxs.value)
      }

      if(hrefPaths === undefined || paths === undefined){
         console.log(paths);
         paths = '/';
         return setHrefPaths(paths),
         valuesEvet();
      }
         setHrefPaths(paths);
         console.log(paths, 'NavBar');
         valuesEvet();

      if(microPaths.includes(paths) === true){
         return setVisible(false),
         setMicroSiteBtn(true);
      };


   }, [hrefPaths , selectedLanguage, openSelect, microSiteBtn])


   useEffect(() => { 
 

      const handleScroll = () => {

      const moving = window.scrollY;

         if(window.innerWidth > 1024 && microPaths.includes(paths) === false){
               setVisible(position > moving);
               setPosition(moving);
               setOpenSelect(false);
               setMomenuOpen(false);
               setMoAsideList(false);
               setMicroSiteBtn(false);
               setonMouseEvet(true);
         }
         // if(window.innerWidth > 1024 && microPaths.includes(paths) === true){
         //    return setMicroSiteBtn(true)
         // }
    }

    const resizeEvet = () => {
         
         const windowWidth = window.innerWidth;
         if(windowWidth <= 1024){

            return window.removeEventListener('scroll', handleScroll),
                   setonMouseEvet(false);

         }setonMouseEvet(true)
         

         // else{
         //    setMomenuOpen(false);
         //    window.addEventListener('scroll', handleScroll);
         // }
     }
     
         window.addEventListener('load' , resizeEvet);
         window.addEventListener('resize', resizeEvet);
         window.addEventListener('scroll', handleScroll);

     return () => {
        window.addEventListener('load' , resizeEvet);
        window.removeEventListener('resize', resizeEvet);
        window.removeEventListener('scroll', handleScroll);
     };
     


   },[position , moMenuOpen , onMouseEvet])



  return (
   <>
    <nav className={visible ? "navBar" : "navBar active"}>
      <div className="logoBoxs">
         <Link to='/'><img src={logo} alt="logo" /></Link>
      </div>
      <ul className={moMenuOpen ? "navLists active" : "navLists"}>
       { navDB.category.map((v,i) => (
         <li key={'nav' + i}><Link to={v.href} className={ hrefPaths ? (hrefPaths === v.href ? "active" : "") : ""}>{v.title}</Link></li>
       ))}
      </ul>
                <div className="languageSelectWrappers">
                  <select
                    className="languageHiddenSelect"
                    id="languageSelectBox"
                    hidden>
                    <option value="KR">KR</option>
                    <option value="EN">EN</option>
                  </select>
                </div>
                <div className={openSelect ? "languageSelects active" : "languageSelects"}>
                  <button className="languageSelectsTitleBtn" style={{backgroundImage:`url(${laguageImg})`}} onClick={openSelectBoxs}>{selectedLanguage}</button>
                  <div className={openSelect ? "languageSelectList active" : "languageSelectList"}>
                    <ul className="languageSelectListMember">
                     {selectValues.filter(value => value !== selectedLanguage).map((value, index) => (
                           <li key={index}>
                              <p onClick={handleSelectClick} style={{backgroundImage:`url(${laguageImg})`}}>{value}</p>
                           </li>
                     ))}
                    </ul>
                  </div>
                </div>
            <div className="moBtnWrapper">
               <button style={{backgroundImage : `url(${moMenuImg})`}} onClick={mobileMenuOpenEvet}></button>
            </div>
            <div className={ microSiteBtn ? "micrositeButtonWrapper active" : "micrositeButtonWrapper"}>
               <button style={ visible ? {backgroundImage : `url(${microArrowUp})`} : {backgroundImage : `url(${microArrowDown})`}} onMouseOver={microSiteMouseOverEvet} onClick={microSiteBtnClickEvet}><span>Main</span></button>
            </div>
        </nav>
         <div className={moMenuOpen ? "moMenus active" : "moMenus"}>
            <ul className="moNavLists" >
               { navDB.category.map((v,i) => (
                  <li key={'moNav' + i}><Link to={v.href} className={ hrefPaths ? (hrefPaths === v.href ? "active" : "") : ""}>{v.title}</Link><em></em></li>
               ))}
               </ul>
            <div className="moAsideMenu">
                <div className="moAsideBtnBoxs" style={ moAsideList ? ({ backgroundImage: `url(${moArrowDownImg})` }) : ({ backgroundImage: `url(${moArrowUpImg})` }) }>
                   <button className={moAsideList ? "active" : ""} style={{ backgroundImage: `url(${mobileAsideImg01})` }} onClick={mobileAsieOpenEvet}></button>
                  <ul className={ moAsideList ?  "moAsideMenuList active" : "moAsideMenuList"}>
                     <li style={{backgroundImage : `url(${moArrowNextImg})`}}><Link to='/'  style={{ backgroundImage: `url(${mobileAsideImg02})` }}></Link></li>
                     <li style={{backgroundImage : `url(${moArrowNextImg})`}}><Link to='/aws' style={{ backgroundImage: `url(${mobileAsideImg03})` }}></Link></li>
                     <li style={{backgroundImage : `url(${moArrowNextImg})`}}><Link to='/googleCloud' style={{ backgroundImage: `url(${mobileAsideImg04})` }}></Link></li>
                     <li style={{backgroundImage : `url(${moArrowNextImg})`}}><Link to='/msAzure' style={{ backgroundImage: `url(${mobileAsideImg05})` }}></Link></li>
  
                  </ul>
                </div>
            </div>
         </div>
    </>
  );
};


export default NavBar;